.nav {
  background-color: grey;
  border-radius: 15px;
  height: 180px;
}

.navBar {
  position: relative;
  margin-left: 25px;
  top: -30px;
}

.navPic {
  position: relative;
  top: 10px;
  left: 620px;
  width: 100px;
  height: 100px;
}

.time {
  margin-left: 1100px;
  margin-top: -90px;
}

.currentTime {
  color: white;
  position: relative;
  left: 50px;
}

.cTime {
  display: inline-block;
  font-size: large;
  position: relative;
  color: white;
}

#timeColor {
  font-size: larger;
}

.storeStatus {
  position: relative;
  top: -20px;
  width: 150px;
}

.Website {
  width: 100%;
  position: relative;
}

body {
  overflow-x: hidden;
}

