@media screen and (min-width: 1920px) {
  .nav {
    width: 1900px;
    background-color: grey;
    border-radius: 15px;
    height: 220px;
  }

  .navBar {
    position: relative;
    margin-left: 25px;
    top: -30px;
  }

  .navPic {
    position: relative;
    top: 10px;
    left: 880px;
    width: 130px;
    height: 130px;
  }

  .time {
    margin-left: 1560px;
    margin-top: -130px;
    font-size: 140%;
  }

  .currentTime {
    color: white;
    position: relative;
    left: 50px;
  }

  .cTime {
    display: inline-block;
    font-size: large;
    position: relative;
    color: white;
  }

  #timeColor {
    font-size: larger;
  }

  .storeStatus {
    position: relative;
    top: -20px;
    width: 250px;
  }

  body {
    width: 98.5%;
    overflow-x: hidden;
  }

  .Website {
    width: 100%;
    position: relative;
    left: 10px;
  }
}
