.modalPage {
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 1000;
  position: fixed;
  right: 0px;
  left: 0px;
  top: 0px;
  bottom: 0px;
}

.modal {
  position: fixed;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: white;
  top: 100px;
  left: 440px;
  background-color: grey;
  width: 460px;
  height: 400px;
  z-index: 1000;
  border: 3px solid saddlebrown;
  border-radius: 15px;
}

.mImage {
  height: 300px;
  width: 200px;
  position: relative;
  left: 10px;
  top: 10px;
  border-radius: 15px;
  border: 1px solid red;
}

.mB {
  font-style: italic;
  border-radius: 15px;
  font-size: medium;
  background-color: darkorange;
  color: navy;
  position: relative;
  left: -160px;
  bottom: -50px;
}

.mB:hover {
  background-color: green;
  color: white;
}

.b {
  font-style: italic;
  border-radius: 15px;
  font-size: medium;
  background-color: darkorange;
  color: navy;
  position: relative;
  left: -50px;
  bottom: -50px;
}

.b:hover {
  background-color: green;
  color: white;
}

.mP {
  font-style: italic;
  font-size: medium;
  color: rgb(2, 70, 2);
  position: relative;
  left: 90px;
  bottom: -10px;
}

.sB {
  font-style: italic;
  border-radius: 15px;
  font-size: medium;
  background-color: darkorange;
  color: navy;
  position: relative;
  left: 276px;
  bottom: 24px;
}

.sB:hover {
  background-color: green;
  color: white;
}

.mInfo {
  font-size: large;
  position: absolute;
  left: 220px;
  top: 50px;
}

