.todaysDeals {
  margin-left: 280px;
  position: relative;
  top: -764px;
}

.homePage {
  height: 1000px;
}

.homePic {
  width: 876px;
  height: 150px;
  position: relative;
  left: 100px;
}

.homeInfo {
  text-align: left;
  margin-left: 10px;
}

.storeHours {
  width: 250px;
  border-radius: 15px;
  text-align: center;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: rgb(194, 3, 3);
  border: 2px solid saddlebrown;
  position: relative;
  top: 20px;
}

.timeInfo {
  font-size: 150%;
  text-align: left;
  position: relative;
  left: 10px;
}

