@media screen and (min-width: 1920px) {
  .todaysDeals {
    margin-left: 280px;
    position: relative;
    top: -764px;
  }

  .homeItems {
    width: 1400px;
    position: relative;
    left: 100px;
  }

  .hr {
    width: 1500px;
  }
  .homePic {
    width: 976px;
    height: 200px;
    position: relative;
    left: 280px;
  }

  .homeInfo {
    text-align: left;
    margin-left: 10px;
  }

  .storeHours {
    width: 250px;
    border-radius: 15px;
    text-align: center;
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    background-color: rgb(194, 3, 3);
    position: relative;
    top: 20px;
  }

  .timeInfo {
    font-size: 150%;
    text-align: left;
    position: relative;
    left: 10px;
  }
}
