@media screen and (min-width: 1920px) {
  .developers {
    background-color: rgb(194, 3, 3);
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    text-align: center;
    position: fixed;
    right: 0px;
    left: 0px;
    bottom: 0px;
    height: 300px;
  }

  .contactInfo {
    font-size: 200%;
    position: relative;
    top: -200px;
  }

  #contactHeader {
    font-size: 250%;
    position: relative;
    top: -200px;
  }

  #contactPic {
    width: 300px;
    height: 200px;
    position: relative;
    left: -700px;
    top: 40px;
  }

  .siteBio {
    width: 500px;
    font-size: large;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    color: white;
    background-color: grey;
    border: 2px solid saddlebrown;
    border-radius: 15px;
    position: relative;
    top: -195px;
  }

  #logoPic {
    position: relative;
    left: 700px;
    top: 20px;
  }
}
