    color: white;
  }

  .viewItem {
    font-style: italic;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    width: 100px;
    border-radius: 15px;
    background-color: grey;
    color: white;
    text-align: center;
  }

  .viewItem:hover {
    border-radius: 15px;
    background-color: saddlebrown;
    color: white;
  }
}
