.pic {
  width: 500px;
  height: 200px;
  position: relative;
  left: 320px;
  margin-top: 10px;
  border: 4px solid saddlebrown;
  border-radius: 15px;
}

.hr {
  border: 2px solid rgb(194, 3, 3);
}
.food {
  font-size: 600%;
  width: 400px;
  -webkit-text-fill-color: pink; /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: red;
  position: relative;
  left: 1050px;
  bottom: -20px;
}

.appliances {
  font-size: 400%;
  width: 400px;
  -webkit-text-fill-color: pink; /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: red;
  position: relative;
  left: 1000px;
  bottom: -30px;
}

.clothing {
  font-size: 450%;
  width: 400px;
  -webkit-text-fill-color: rgb(
    247,
    132,
    151
  ); /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: red;
  position: relative;
  left: 1040px;
  bottom: -30px;
}

.tech {
  font-size: 400%;
  width: 400px;
  -webkit-text-fill-color: rgb(
    240,
    183,
    193
  ); /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: red;
  position: relative;
  left: 1000px;
  bottom: -30px;
}

.furniture {
  font-size: 420%;
  width: 400px;
  -webkit-text-fill-color: rgb(
    207,
    117,
    132
  ); /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: red;
  position: relative;
  left: 1040px;
  bottom: -30px;
}

.items {
  width: 80%;
  margin-left: 250px;
  margin-top: -200px;
}

.sidebar {
  width: 215px;
  background-color: rgb(194, 3, 3);
  color: white;
  border-radius: 15px;
  margin-top: 60px;
  border: 1px solid saddlebrown;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.foodPicDiv {
  background-image: url("../Images/Food.jpg");
  background-repeat: no-repeat;
  position: relative;
  /*right: 5px;*/
  margin-top: -70px;
  height: 280px;
  border-radius: 15px;
}

.appliancesPicDiv {
  background-image: url("../Images/Appliances.jpg");
  background-repeat: no-repeat;
  position: relative;
  /*right: 5px;*/
  margin-top: -40px;
  height: 280px;
  border-radius: 15px;
}

.techPicDiv {
  background-image: url("../Images/Technology.jpg");
  background-repeat: no-repeat;
  position: relative;
  /*right: 5px;*/
  margin-top: -40px;
  height: 280px;
  border-radius: 15px;
}

.clothingPicDiv {
  background-image: url("../Images/Clothing.jpg");
  background-repeat: no-repeat;
  position: relative;
  /*right: 5px;*/
  margin-top: -50px;
  height: 280px;
  border-radius: 15px;
}

.furniturePicDiv {
  background-image: url("../Images/Furniture.jpg");
  background-repeat: no-repeat;
  position: relative;
  /*right: 5px;*/
  margin-top: -40px;
  height: 280px;
  border-radius: 15px;
}

